/* eslint-disable import/no-extraneous-dependencies */
import React from 'react';
import styled from 'styled-components';
import { useFetchWeather } from '../../hooks';
import WeatherHeader from './WeatherHeader';
import WeatherHourly from './WeatherHourly';
import WeatherDaily from './WeatherDaily';
import { WidgetWrapper } from '../styled/index';

const WeatherWrapper = styled(WidgetWrapper)`
  flex-direction: column;
  align-items: center;
  display: flex;
  overflow: hidden;
  color: ${(props) => props.theme.color.text};

  h1 {
    margin: 5px;
    font-size: 25px;
    color: inherit;
  }

  h2 {
    color: inherit;
  }

  p {
    color: ${(props) =>
    props.theme.name === 'day' ? props.theme.color.text : '#b5def4'};
    font-size: 18px;
    padding-right: 8px;
    padding-left: 8px;
  }

  span {
    font-size: 18px;
    padding-right: 8px;
    padding-left: 8px;
    color: inherit;
  }
`;

const Hr = styled.div`
  width: 90%;
  height: 0;
  border-bottom: solid 1px ${(props) => props.theme.color.textSub};
  opacity: 0.4;
  margin: 10px 0;
`;

interface WeatherWidgetProps {
  apiKey: string;
}

function WeatherWidget({ apiKey }: WeatherWidgetProps) {
  const [errorMsg, loadingMsg, weatherData] = useFetchWeather(apiKey, 'metric');

  const renderWediget = () => {
    if (loadingMsg) {
      return (
        <p style={{ fontSize: '30px', marginTop: '20px' }}>{loadingMsg}</p>
      );
    }

    if (errorMsg) {
      return <p style={{ fontSize: '30px', marginTop: '20px' }}>{errorMsg}</p>;
    }

    return (
      <>
        <WeatherHeader
          today={(weatherData as WeatherData).daily[0]}
          current={(weatherData as WeatherData).current}
          timezone={(weatherData as WeatherData).timezone}
        />
        <Hr />
        <WeatherHourly hourly={(weatherData as WeatherData).hourly} />
        <Hr />
        <WeatherDaily daily={(weatherData as WeatherData).daily} />
      </>
    );
  };

  return (
    <WeatherWrapper className="neumorphic">{renderWediget()}</WeatherWrapper>
  );
}

export default WeatherWidget;
