/* eslint-disable @typescript-eslint/no-explicit-any */
import React, { useContext } from 'react';
import { ThemeContext } from 'styled-components';
import { Chart, Line } from 'bizcharts';

export interface TempChartProps {
  data: {
    hour: string;
    value: number;
  }[];
  width: number | string;
  height: number | string;
  padding?: number[];
  pure: boolean;
}

function TempChart({ data, width, height, padding, pure }: TempChartProps) {
  const themeContext = useContext<AppTheme>(ThemeContext);
  return (
    <div>
      <Chart
        padding={padding}
        autoFit
        height={height}
        width={width}
        data={data}
        pure={pure}
      >
        <Line
          position="hour*value"
          color={themeContext.color.text}
          label={[
            'value',
            (v) => {
              return {
                content: `${v}°C`,
                style: {
                  fill: themeContext.color.text,
                },
              };
            },
          ]}
        />
      </Chart>
    </div>
  );
}

export default TempChart;
