import React, { useMemo } from 'react';
import styled from 'styled-components';
import { widgetPickerIcons } from '../../assets/img';
import { GridHeader } from '../styled';

const Wrapper = styled.div`
  padding: 0px 20px 20px 20px;
  display: flex;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  @media (max-width: 1024px) {
    flex-direction: space-bween;
  }
`;

const IconWrapper = styled.div`
  position: relative;
  display: flex;
  align-items: center;
  @media (max-width: 1024px) {
    align-items: flex-end;
  }
  justify-content: center;
  height: 100%;
`;

const Icon = styled.img`
  height: 170px;
  width: 170px;
`;

function WidgetPicker() {
  const memodedWidgets = useMemo(() => {
    return Object.keys(widgetPickerIcons).map((key) => {
      return (
        <IconWrapper key={key}>
          <GridHeader>{key}</GridHeader>
          <Icon
            className="neumorphic-convex"
            alt={key}
            src={(widgetPickerIcons as Dictionary<string>)[key]}
          />
        </IconWrapper>
      );
    });
  }, []);

  return <Wrapper className="neumorphic">{memodedWidgets}</Wrapper>;
}

export default WidgetPicker;
