import React, { useState, useContext } from 'react';
import styled from 'styled-components';
import { UnorderedListOutlined, ApartmentOutlined } from '@ant-design/icons';
import { observer } from 'mobx-react';
import moment from 'moment';
import { ToDoStoreCtx, FORMAT } from '../../store/toDoStore';
import ToDoInput from './ToDoInput';
import ToDoFlow from './ToDoFlow';
import ToDoList from './ToDoList';

const ToDoWidgetWrapper = styled.div`
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  overflow-y: auto;
  color: ${props => props.theme.color.text};
  > h2 {
    color: ${props => props.theme.color.textSub};
  }
`;

const ToggleViewWrapper = styled.div`
  position: absolute;
  left: 0px;
  top: 65px;
  width: 10%;
  text-align: justify;
  span {
    font-size: 35px;
    width: 100%;
    text-align: center;
  }
  p {
    text-align: center;
    color: ${props => props.theme.color.textSub};
    font-size: ${props => `calc(${props.theme.font.sm}*0.8)`};
    font-weight: bold;
  }
`;

enum View {
  Flow,
  List,
}

const ToDoWidget = () => {
  const toDoStore = useContext(ToDoStoreCtx);
  const [date, setDate] = useState(moment(new Date()));
  const [view, toggleView] = useState<View>(View.List);

  const renderToggle = () => {
    return (
      <ToggleViewWrapper>
        {view === View.List ? (
          <ApartmentOutlined
            onClick={() => {
              toggleView(View.Flow);
            }}
          />
        ) : (
          <UnorderedListOutlined
            onClick={() => {
              toggleView(View.List);
            }}
          />
        )}
        <p>{view === View.List ? 'Flow' : 'List'}</p>
      </ToggleViewWrapper>
    );
  };

  const renderView = () => {
    if (date.format(FORMAT) in (toDoStore.toDoList as ToDoList)) {
      return view === View.List ? (
        <ToDoList date={date} />
      ) : (
        <ToDoFlow date={date} />
      );
    }
    return <h2>Nothing to do</h2>;
  };

  return (
    <ToDoWidgetWrapper className="neumorphic">
      <ToDoInput setDate={setDate} date={date} />
      {renderToggle()}
      {renderView()}
    </ToDoWidgetWrapper>
  );
};

export default observer(ToDoWidget);
