import React, { useContext } from 'react';
import styled, { ThemeContext } from 'styled-components';
import { Slider } from 'antd';
import { GridHeader } from '../styled';

const Wrapper = styled.div`
  padding: 20px;
  display: flex;
  position: relative;
  align-items: center;
  justify-content: center;
  flex-direction: column;

  .temperature-display {
    span {
      font-size: ${(props) => props.theme.font.md};
      vertical-align: baseline;
      color: gray;
    }
    font-size: ${(props) => props.theme.font.lg};
    font-family: 'Roboto', 'Helvetica', 'Arial', sans-serif;
    font-weight: 400;
    line-height: 1.167;
    letter-spacing: 0em;
    color: ${(props) => props.theme.color.text};
  }

  /* style for hover */
  .ant-slider:hover.ant-slider-with-marks {
    .ant-slider-track {
      background-color: transparent;
      opacity: 1;
    }

    .ant-slider-rail {
      opacity: 1;
    }

    .ant-slider-handle {
      box-shadow: 0px 0px 0px 6px rgba(0, 0, 0, 0.05);
      background-color: rgba(255, 255, 255, 1);
    }
  }

  /* style for active */
  .ant-slider:active.ant-slider-with-marks {
    .ant-slider-handle {
      box-shadow: 0px 0px 0px 10px rgba(0, 0, 0, 0.05);
    }
  }

  /* style for not hover */
  .ant-slider.ant-slider-with-marks {
    width: 80%;
    height: 80px;
    display: flex;
    align-items: center;
    margin-bottom: 0;

    .ant-slider-rail {
      transition: opacity 0.3s;
      opacity: 0.8;
      border-radius: 10px;
      height: 20px;
      background: linear-gradient(
        90deg,
        rgba(140, 207, 255, 1) 0%,
        rgba(250, 0, 0, 1) 100%
      );
    }

    .ant-slider-step {
      display: none;
    }

    .ant-slider-track {
      transition: opacity 0.3s;
      opacity: 0.8;
      border-radius: 10px;
      height: 20px;
      background: transparent;
    }

    .ant-slider-handle {
      transition: background-color 0.3s;
      height: 30px;
      width: 30px;
      border: solid 0px;
      background-color: rgba(255, 255, 255, 0.9);
      box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2),
        0 3px 10px 0 rgba(0, 0, 0, 0.19);
      transition: box-shadow 150ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    }

    .ant-slider-mark {
      top: 24px;
    }
  }
`;

interface TemperatureSliderProps {
  temperature: number;
  setTemperature: (temperature: number) => void;
  maxTemp: number;
  minTemp: number;
}

function TemperatureSlider({
  temperature,
  setTemperature,
  maxTemp,
  minTemp,
}: TemperatureSliderProps) {
  const theme = useContext<AppTheme>(ThemeContext);

  // customize marks on the slider
  const marks = {
    [minTemp]: {
      style: {
        color: '#91d5ff',
        fontSize: theme.font.sm,
        transform: 'translate(-50%, 25px)',
      },
      label: <strong>{minTemp}°C</strong>,
    },
    [Math.floor((minTemp + maxTemp) / 2)]: {
      style: {
        color: '#c36b80',
        fontSize: theme.font.sm,
        transform: 'translate(-50%, 25px)',
      },
      label: <strong>{Math.floor((minTemp + maxTemp) / 2)}°C</strong>,
    },
    [maxTemp]: {
      style: {
        color: '#f50',
        fontSize: theme.font.sm,
        transform: 'translate(-50%, 25px)',
      },
      label: <strong>{maxTemp}°C</strong>,
    },
  };

  const handleTemperatureChange = (value: number): void => {
    setTemperature(value);
  };

  const toolTipFormatter = (value?: number | undefined) => {
    return `${value}°C`;
  };

  return (
    <Wrapper className="temperature-slider-wapper neumorphic">
      <GridHeader>Temperature</GridHeader>
      <h1 className="temperature-display">
        {temperature}°C
      </h1>
      <Slider
        marks={marks}
        value={temperature}
        min={minTemp}
        max={maxTemp}
        onChange={handleTemperatureChange}
        tipFormatter={toolTipFormatter}
      />
    </Wrapper>
  );
}

export default TemperatureSlider;
