/* eslint-disable @typescript-eslint/no-explicit-any */
import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { DragOutlined, EditFilled, EditOutlined } from '@ant-design/icons';
import GridLayout, { WidthProvider } from 'react-grid-layout';
import { widgets, saveLayout } from './WidgetPage.config';

const createBtn = (antdIcon: any) => {
  return styled(antdIcon)`
    font-size: 30px;
    margin-top: 10px;
  `;
};

const EditBtnFilled = createBtn(EditFilled);
const EditBtnOutlined = createBtn(EditOutlined);

const WidgetPageWrapper = styled.div`
  background-color: ${(props) => props.theme.color.main};
  height: ${(props) => props.theme.content.height};
  min-width: ${(props) => props.theme.app.minWidth};
  color: ${(props) => props.theme.color.text};
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  overflow-x: hidden;
  justify-content: space-evenly;
  position: relative;
  padding: 20px;
  .layout {
    width: 100%;
    top: 20px;
  }

  .react-resizable-handle {
    z-index: 11;
    width: 50px !important;
    height: 50px !important;
    &::after {
      border-right: 0.5em solid white !important;
      border-bottom: 0.5em solid white !important;
      width: 90% !important;
      height: 90% !important;
    }
  }

  .react-grid-item {
    touch-action: auto !important;
  }
`;

const GridLayoutWithWidth = WidthProvider(GridLayout);

const DragBtn = styled(DragOutlined)`
  position: absolute;
  left: 0;
  z-index: 10;
  height: 100%;
  width: 100%;
  font-size: 100px;
  color: white;
  background: rgba(0, 0, 0, 0.4);
  display: flex;
  align-items: center;
  justify-content: center;
`;

const WidgetControlWrapper = styled.div`
  height: 50px;
  width: 100%;
  padding-left: 10px;
  display: flex;
  vertical-align: middle;
  > button {
    font-size: 20px;
    font-weight: 500;
    margin-top: -4px;
    color: gray;
    cursor: pointer;
    border: 0px;
    background: transparent;
    &:focus {
      outline: 0px;
    }
  }
`;

const SelectBtn = styled.button`
  background: transparent;
  border: none;
  &:focus {
    border: none;
    outline: none;
  }
  margin-left: 10px;
`;

const WidgetContainer = styled.div`
  position: relative;
  height: 100%;
  width: 100%;
`;

function WidgetPage() {
  // toggle edit mode
  const [isEditing, setEditing] = useState<boolean>(false);

  // control selected status
  const [selected, setSelected] = useState(widgets);

  // control layout change
  const [layout, setLayout] = useState<GridLayout.Layout[]>(
    Object.entries(widgets)
      .filter(([, config]) => config.selected)
      .map(([id, config]) => {
        return {
          i: id,
          ...config.initialLayout,
          isResizable: isEditing,
          static: !isEditing,
        };
      })
  );

  /* update layout when:
   *   1. edit mode change
   *   2. widget show/hide (selected) change
   */
  useEffect(() => {
    setLayout((currentLayout) => {
      const newLayout = Object.entries(widgets)
        // only consider selected widgets
        .filter(([id]) => selected[id].selected)
        .map(([id, config]) => {
          const attr = {
            i: id,
            isResizable: isEditing,
            static: !isEditing,
          };
          let widgetLayout;
          const currentWidgetConfig = currentLayout.filter(
            (l) => l.i === id
          )[0];

          // this is a hack to check if it is selected previously
          if (currentWidgetConfig.w === 1 && currentWidgetConfig.h === 1) {
            widgetLayout = {
              // if not selected previously, inject default setting
              // NOTE: if not inject default setting here, new layout will be initialized with widht = 1 and height = 1, this is why we need the above if statement
              ...config.initialLayout,
              ...attr,
            };
          } else {
            widgetLayout = {
              // if previously selected, inject current layout
              ...currentWidgetConfig,
              ...attr,
            };
          }
          return widgetLayout;
        });

      // save new layout to local storage
      saveLayout(selected, newLayout);
      return newLayout;
    });
  }, [isEditing, selected]);

  // classname for grid layout drag handling
  const dragHanldeClassName = 'handle';

  // render toggle menu (edit mode)
  const renderWidgetToggleList = () => {
    if (!isEditing) return null;
    return Object.entries(selected).map(([id, config]) => {
      return (
        <SelectBtn
          onClick={() =>
            setSelected({
              ...selected,
              [id]: { ...config, selected: !config.selected },
            })}
          type="button"
          key={id}
        >
          {config.selected ? config.iconSelected : config.iconUnSelected}
        </SelectBtn>
      );
    });
  };

  // render edit button
  const renderEditBtn = () => {
    if (isEditing) {
      return (
        <>
          {/* <button type="button" onClick={() => setEditing(!isEditing)}>
            Save
          </button> */}
          <EditBtnFilled onClick={() => setEditing(!isEditing)} />
        </>
      );
    }
    return (
      <>
        {/* <button type="button" onClick={() => setEditing(!isEditing)}>
          Edit
        </button> */}
        <EditBtnOutlined onClick={() => setEditing(!isEditing)} />
      </>
    );
  };

  // render widgets
  const renderWidgets = () => {
    return (
      Object.entries(widgets)
        // only render widgets that are selected
        .filter(([id]) => selected[id].selected)
        .map(([id, config]) => {
          return (
            <WidgetContainer onTouchMove={(e) => e.stopPropagation()} key={id}>
              {isEditing ? <DragBtn className={dragHanldeClassName} /> : null}
              {config.component}
            </WidgetContainer>
          );
        })
    );
  };

  return (
    <WidgetPageWrapper>
      <WidgetControlWrapper className="neumorphic">
        {renderEditBtn()}
        {renderWidgetToggleList()}
      </WidgetControlWrapper>

      <GridLayoutWithWidth
        className="layout"
        layout={layout}
        cols={64}
        rowHeight={1}
        draggableHandle={`.${dragHanldeClassName}`}
        onLayoutChange={(updatedLayout) => {
          setLayout(updatedLayout);
          saveLayout(selected, updatedLayout);
        }}
        isDroppable={true}
      >
        {renderWidgets()}
      </GridLayoutWithWidth>
    </WidgetPageWrapper>
  );
}

export default WidgetPage;
