/* eslint-disable no-param-reassign */
import React, { useEffect, useState, useRef, useContext } from 'react';
import styled from 'styled-components';
import { observer } from 'mobx-react';
import { ProfileStoreCtx } from '../../../store/profileStore';
import { TimeCountdown, VolumeCountdown } from '../../../components/countDown';
import TemperatureSlider from '../../../components/temperatureSlider';
import WaterSlider from '../../../components/waterSlider';
import VoiceControlBtn from '../../../components/voiceControlBtn';
import WidgetPicker from '../../../components/widgetPicker';
import Menu from './TemperatureMenu';
import { GridHeader } from '../../../components/styled';
import { StartMode, ShowerState, CONST } from '../../../assets/const';

const TemperaturePageWrapper = styled.div`
  height: ${(props) => props.theme.content.height};
  min-width: ${(props) => props.theme.app.minWidth};
  display: grid;
  position: relative;
  gap: 20px;
  padding: 20px 20px 20px 20px;
  grid-template-columns: auto autp;
  grid-template-rows: 50px auto auto;
  background-color: ${(props) => props.theme.color.main};
`;

const CountDownPicker = styled.div`
  padding: 0px 20px 20px 20px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  @media (max-width: 1024px) {
    justify-content: space-between;
  }
  position: relative;
  height: 100%;
  width: 100%;
`;

const PickerItem = styled.div`
  position: relative;
  display: flex;
  align-items: center;
  @media (max-width: 1024px) {
    align-items: flex-end;
  }
  justify-content: center;
  height: 100%;
  transition: all 0.5s linear;
`;

const fadeIn = (element: null | HTMLElement | undefined) => {
  if (element) {
    element.style.transform = 'scale(1)';
  }
};

const fadeOut = (element: null | HTMLElement | undefined) => {
  if (element) {
    element.style.transform = 'scale(0)';
  }
};

const moveToLeft = (element: null | HTMLElement | undefined) => {
  if (element) {
    element.style.transform = 'translate(-250px, 0%)';
    element.style.fontSize = '4rem';
  }
};

const moveToRight = (element: null | HTMLElement | undefined) => {
  if (element) {
    element.style.transform = 'translate(150px, 0%)';
    element.style.fontSize = '4rem';
  }
};

const moveOut = (element: null | HTMLElement | undefined) => {
  if (element) {
    element.style.transform = 'translate(-50%, 15%)';
    element.style.fontSize = '3rem';
  }
};

function TemperaturePage({
  startMode,
  setStartMode,
  timer,
  timerCountdown,
  finished,
  setVolume,
  volume,
  volumeCountdown,
  showerState,
  setShowerState,
  setTimerCountdown,
  setVolumeCountdown,
}: {
  startMode: StartMode;
  setStartMode: React.Dispatch<React.SetStateAction<StartMode>>;
  finished: boolean;
  setTimerCountdown: React.Dispatch<React.SetStateAction<number>>;
  setVolumeCountdown: React.Dispatch<React.SetStateAction<number>>;
} & VolumeCountdownControl &
TimeCountdownControl &
ShowerControl<ShowerState>) {
  const { currentUser } = useContext(ProfileStoreCtx);
  const minTemp = currentUser
    ? currentUser.temperatureRange[0]
    : CONST.MIN_TEMP;
  const maxTemp = currentUser
    ? currentUser.temperatureRange[1]
    : CONST.MAX_TEMP;
  const [temperature, setTemperature] = useState<number>(
    Math.floor((minTemp + maxTemp) / 2)
  );
  const picker = useRef<null | HTMLDivElement>(null);

  useEffect(() => {
    setTemperature(Math.floor((minTemp + maxTemp) / 2));
  }, [minTemp, maxTemp]);

  useEffect(() => {
    if (startMode === StartMode.Time) {
      fadeOut(picker.current?.childNodes[1] as HTMLElement);
      moveToRight(
        picker.current?.childNodes[0].childNodes[1].childNodes[2] as HTMLElement
      );
    }
    if (startMode === StartMode.Volume) {
      fadeOut(picker.current?.childNodes[0] as HTMLElement);
      moveToLeft(
        picker.current?.childNodes[1].childNodes[1].childNodes[1] as HTMLElement
      );
    }
    if (showerState === ShowerState.Inited) {
      fadeIn(picker.current?.childNodes[0] as HTMLElement);
      fadeIn(picker.current?.childNodes[1] as HTMLElement);
      moveOut(
        picker.current?.childNodes[0].childNodes[1].childNodes[2] as HTMLElement
      );
      moveOut(
        picker.current?.childNodes[1].childNodes[1].childNodes[1] as HTMLElement
      );
    }
  }, [startMode, picker, showerState]);

  return (
    <TemperaturePageWrapper>
      <Menu startMode={startMode}>
        <VoiceControlBtn
          minTemp={minTemp}
          maxTemp={maxTemp}
          temperature={temperature}
          setTemperature={setTemperature}
          setVolume={setVolume}
          volume={volume}
          showerState={showerState}
          startMode={startMode}
          timer={timer}
          setTimerCountdown={setTimerCountdown}
          setVolumeCountdown={setVolumeCountdown}
        />
      </Menu>
      <TemperatureSlider
        temperature={temperature}
        setTemperature={setTemperature}
        minTemp={minTemp}
        maxTemp={maxTemp}
      />
      <WaterSlider finished={finished} />
      <CountDownPicker ref={picker} className="neumorphic">
        <PickerItem>
          <GridHeader> Time </GridHeader>
          <TimeCountdown
            setStartMode={setStartMode}
            timer={timer}
            timerCountdown={timerCountdown}
            atHeader={false}
            showerState={showerState}
            setShowerState={setShowerState}
          />
        </PickerItem>
        <PickerItem>
          <GridHeader> Volume </GridHeader>
          <VolumeCountdown
            setStartMode={setStartMode}
            internval={CONST.VOLUME_CHECK_INTERNVAL}
            setVolume={setVolume}
            volume={volume}
            volumeCountdown={volumeCountdown}
            showerState={showerState}
            setShowerState={setShowerState}
            atHeader={false}
          />
        </PickerItem>
      </CountDownPicker>
      <WidgetPicker />
    </TemperaturePageWrapper>
  );
}

export default observer(TemperaturePage);
