/* eslint-disable import/no-extraneous-dependencies, @typescript-eslint/no-explicit-any */
import React from 'react';
import styled from 'styled-components';
import {
  BulbFilled,
  BulbOutlined
} from '@ant-design/icons';
import { useToggle } from '../../../hooks';
import Guide from './Guide';
import { StartMode } from '../../../assets/const';

const createBtn = (antdIcon: any) => {
  return styled(antdIcon)`
    font-size: 30px;
    margin-top: 10px;
  `;
};

const GuideBtnOutlined = createBtn(BulbOutlined);
const GuideBtnFilled = createBtn(BulbFilled);

const Menu = styled.div`
  position: relative;
  height: 50px;
  width: 100%;
  top: 0px;
  left: 0px;
  text-align: left;
  grid-column: 1 / span 2;
  grid-row: 1 / span 1;
  gap: 10px;
  padding-left: 10px;
  display: flex;
  .anticon {
    color: ${(props) => props.theme.color.text};
  }
`;

interface Props {
  startMode: StartMode;
  children: React.ReactNode;
}

function TemperatureMenu({ startMode, children }: Props) {
  const [isGuideOpen, toggleGuide] = useToggle(false);

  return (
    <>
      <Guide
        isOpen={isGuideOpen}
        toggleGuide={toggleGuide}
        startMode={startMode}
      />
      <Menu className="neumorphic">
        {/* Guide Display */}
        {isGuideOpen ? (
          <GuideBtnFilled onClick={toggleGuide} />
        ) : (
          <GuideBtnOutlined onClick={toggleGuide} />
        )}

        {/* Voice Control Display */}
        {children}
      </Menu>
    </>
  );
}

export default TemperatureMenu;
