/* eslint-disable @typescript-eslint/no-explicit-any */
import React, { useMemo, useEffect } from 'react';
import styled from 'styled-components';
import { message } from 'antd';
import { debounce } from 'lodash';
import { AudioOutlined, AudioFilled } from '@ant-design/icons';
import SpeechRecognition, {
  useSpeechRecognition,
} from 'react-speech-recognition';
import { ShowerState, StartMode, CONST } from '../../assets/const';

const createBtn = (antdIcon: any) => {
  return styled(antdIcon)`
    font-size: 30px;
    margin-top: 10px;
  `;
};

const AudioBtnOutlined = createBtn(AudioOutlined);
const AudioBtnFilled = createBtn(AudioFilled);

const CommandWrapper = styled.div`
  display: flex;
  flex-direction: column;
  text-align: left;
`;

const DD = styled.dd`
  margin-left: 5%;
`;

const Transcript = styled.p`
  display: inline-block;
  margin-top: 5px;
  font-size: ${(props) => `calc(${props.theme.font.sm}*1)`};
  width: 100%;
  text-align: left;
  top: 20px;
  color: ${(props) => props.theme.color.text};
  z-index: 3;
`;

const ErrorMessage = (
  <>
    Invalid Voice Command
    <CommandWrapper>
      <hr />
      <dl>
        <dt>
          <span role="img" aria-label="speaker">
            🔊
          </span>
          'increase by x'
        </dt>
        <DD> - increase x°C</DD>
        <dt>
          <span role="img" aria-label="speaker">
            🔊
          </span>
          'decrease by x'
        </dt>
        <DD> - decrease x°C</DD>
        <dt>
          <span role="img" aria-label="speaker">
            🔊
          </span>
          'hotter'
        </dt>
        <DD> - increase 1°C</DD>
        <dt>
          <span role="img" aria-label="speaker">
            🔊
          </span>
          'cooler' or 'colder'
        </dt>
        <DD> - decrease 1°C</DD>
        <dt>
          <span role="img" aria-label="speaker">
            🔊
          </span>
          'stop'
        </dt>
        <DD> - stop voice detection</DD>
      </dl>
    </CommandWrapper>
  </>
);

interface VoiceControlBtnProps {
  temperature: number;
  setTemperature: (temperature: number) => void;
  maxTemp: number;
  minTemp: number;
  volume: number;
  setVolume: React.Dispatch<React.SetStateAction<number>>;
  showerState: ShowerState;
  startMode: StartMode;
  timer: TimerRenderProps;
  setTimerCountdown: React.Dispatch<React.SetStateAction<number>>;
  setVolumeCountdown: React.Dispatch<React.SetStateAction<number>>;
}

function VoiceControlBtn({
  setTemperature,
  temperature,
  maxTemp,
  minTemp,
  setVolume,
  volume,
  showerState,
  startMode,
  timer,
  setTimerCountdown,
  setVolumeCountdown,
}: VoiceControlBtnProps) {
  const commands = [
    {
      command: 'increase by *',
      callback: (val: string) => {
        const t = parseInt(val, 10);
        if (isNaN(t)) return;
        setTemperature(Math.min(maxTemp, temperature + t));
      },
    },
    {
      command: 'decrease by *',
      callback: (val: string) => {
        const t = parseInt(val, 10);
        if (isNaN(t)) return;
        setTemperature(Math.max(minTemp, temperature - t));
      },
    },
    {
      command: 'cooler',
      callback: () => {
        setTemperature(Math.max(minTemp, temperature - 1));
      },
      isFuzzyMatch: true,
    },
    {
      command: 'colder',
      callback: () => {
        setTemperature(Math.max(minTemp, temperature - 1));
      },
      isFuzzyMatch: true,
    },
    {
      command: 'hotter',
      callback: () => {
        setTemperature(Math.min(maxTemp, temperature + 1));
      },
      isFuzzyMatch: true,
    },
    {
      command: 'extend',
      callback: () => {
        if (showerState === ShowerState.Started) {
          switch (startMode) {
            case StartMode.Time:
              timer.setTime(timer.getTime() + CONST.TIME_STEP * 60 * 1000);
              setTimerCountdown(
                (current) => current + CONST.TIME_STEP * 60 * 1000
              );
              break;
            case StartMode.Volume:
              setVolume(volume + CONST.VOLUME_STEP);
              setVolumeCountdown((current) => current + CONST.VOLUME_STEP);
              break;
            default:
          }
        }
      },
      isFuzzyMatch: true,
    },
    {
      command: 'stop',
      callback: () => {
        resetTranscript();
        SpeechRecognition.stopListening();
      },
    },
  ];

  const { listening, transcript, resetTranscript } = useSpeechRecognition({
    commands,
  });

  // clearup transcripts
  const clearTranscript = useMemo(
    () =>
      debounce((ts: string) => {
        const validation = 
          /^(extend|increase|decrease|cooler|colder|hotter|stop)/g;
        if (listening && ts && !ts.match(validation))
          message.error(ErrorMessage, 3);
        if (listening) resetTranscript();
      }, 2000),
    [resetTranscript, listening]
  );

  useEffect(() => {
    return clearTranscript(transcript);
  }, [transcript, clearTranscript]);

  // check if browser supports speech api
  if (!SpeechRecognition.browserSupportsSpeechRecognition()) {
    return null;
  }

  return (
    <div className="voice-btn-wapper">
      {!listening ? (
        <AudioBtnOutlined
          onClick={(): void => {
            resetTranscript();
            SpeechRecognition.startListening({ continuous: true });
          }}
        />
      ) : (
        <>
          <AudioBtnFilled onClick={SpeechRecognition.stopListening} />
          <Transcript>{transcript}</Transcript>
        </>
      )}
    </div>
  );
}

export default VoiceControlBtn;
