export const AppLayout = styled(Layout)`
  min-width: ${(props) => props.theme.app.minWidth};

  .neumorphic {
    border-radius: 33px;
    background: ${(props) => props.theme.color.main};
    box-shadow: inset 10px 10px 20px ${(props) => props.theme.color.sub1},
      inset -10px -10px 20px ${(props) => props.theme.color.sub2};
  }

  .neumorphic-convex {
    border-radius: 50%;
    background: linear-gradient(
      145deg,
      ${(props) => props.theme.color.sub2},
      ${(props) => props.theme.color.sub1}
    );
    box-shadow: 5px 5px 14px ${(props) => props.theme.color.sub1},
      -5px -5px 14px ${(props) => props.theme.color.sub2};
  }
`;

export const AppContent = styled(Layout.Content)`
  width: 100vw;
  height: ${(props) => props.theme.content.height};
  align-items: stretch;
  min-width: ${(props) => props.theme.app.minWidth};
`;

