import React from 'react';
import styled from 'styled-components';
import { Layout } from 'antd';
import { BasicProps } from 'antd/lib/layout/layout';
import { useClock } from '../../hooks/index';
import { formatDate } from '../../utils/index';
import { TimeCountdown, VolumeCountdown } from '../countDown';
import UserSeletcor from './UserSelector';
import { StartMode, ShowerState, CONST } from '../../assets/const';

const Header = styled(Layout.Header)`
  position: relative;
  color: ${(props) => props.theme.color.text};
  width: 100%;
  margin: auto auto;
  box-shadow: 0 0 10px #000000;
  font-weight: 800;
  background-color: ${(props) => props.theme.color.main};
  display: flex;
  height: ${(props) => props.theme.header.height};
  z-index: 0;
  transition: all 0.5s ease-in-out;
  .app-clock {
    font-size: ${(props) => props.theme.font.sm};
    color: ${(props) => props.theme.color.text};
    margin: auto;
  }
  .app-title {
    margin: auto;
    position: relative;
    flex-grow: 1;
    color: ${(props) => props.theme.color.text};
    text-align: center;
    font-size: ${(props) => props.theme.font.sm};
    font-family: 'Roboto', 'Helvetica', 'Arial', sans-serif;
    font-weight: bold;
    letter-spacing: 0.0075rem;
    /* right: -100px; */
  }
  .theme-btn {
    position: absolute;
    border: none;
    font-size: 1.4rem;
    right: 230px;
    width: 90px;
    font-weight: bold;
    height: 100%;
    cursor: pointer;
    padding: 0.2em 0 0 0;
    text-align: center;
    .l {
      background-color: rgba(63, 63, 63, 0.7);
      border-radius: 0.75em;
      box-shadow: 0.125em 0.125em 0 0.125em rgba(0, 0, 0, 0.3) inset;
      color: #fdea7b;
      display: inline-flex;
      align-items: center;
      margin: auto;
      padding: 0.15em;
      width: 3em;
      height: 1.5em;
      transition: background-color 0.1s 0.3s ease-out,
        box-shadow 0.1s 0.3s ease-out;
      -webkit-appearance: none;
      -moz-appearance: none;
      appearance: none;
    }
    .l:before,
    .l:after {
      content: '';
      display: block;
    }
    .l:before {
      background-color: #d7d7d7;
      border-radius: 50%;
      width: 1.2em;
      height: 1.2em;
      transition: background-color 0.1s 0.3s ease-out, transform 0.3s ease-out;
      z-index: 1;
    }
    .l:after {
      background: linear-gradient(transparent 50%, rgba(0, 0, 0, 0.15) 0) 0 50% /
          50% 100%,
        repeating-linear-gradient(
            90deg,
            #bbb 0,
            #bbb,
            #bbb 20%,
            #999 20%,
            #999 40%
          )
          0 50% / 50% 100%,
        radial-gradient(circle at 50% 50%, #888 25%, transparent 26%);
      background-repeat: no-repeat;
      border: 0.25em solid transparent;
      border-left: 0.4em solid #d8d8d8;
      border-right: 0 solid transparent;
      transition: border-left-color 0.1s 0.3s ease-out, transform 0.3s ease-out;
      transform: translateX(-22.5%);
      transform-origin: 25% 50%;
      width: 1.2em;
      height: 1em;
    }
    /* Checked */
    .l:checked {
      background-color: rgba(0, 0, 0, 0.45);
      box-shadow: 0.125em 0.125em 0 0.125em rgba(0, 0, 0, 0.1) inset;
    }
    .l:checked:before {
      background-color: currentColor;
      transform: translateX(125%);
    }
    .l:checked:after {
      border-left-color: currentColor;
      transform: translateX(-2.5%) rotateY(180deg);
    }
    /* Other States */
    .l:focus {
      /* Usually an anti-A11Y practice but set to remove an annoyance just for this demo */
      outline: 0;
    }
  }
`;

const TimerWrapper = styled.div`
  position: absolute;
  right: 320px;
  width: 90px;
  height: 100%;
  top: 0;
  overflow: hidden;
`;

interface Props {
  setTheme: React.Dispatch<React.SetStateAction<boolean>>;
  isDayTheme: boolean;
  finished: boolean;
  startMode: StartMode;
}

function AppHeader({
  setTheme,
  startMode,
  isDayTheme,
  timer,
  timerCountdown,
  finished,
  setVolume,
  volume,
  volumeCountdown,
  showerState,
  setShowerState,
  ...props
}: BasicProps &
Props &
VolumeCountdownControl &
TimeCountdownControl &
ShowerControl<ShowerState>) {
  const date = new Date();
  const clock = useClock(date);

  return (
    <Header {...props}>
      <p className="app-clock">{formatDate(date)}</p>
      <h6 className="app-title"> Smart Shower </h6>
      <p className="app-clock">{clock.toLocaleTimeString()}</p>
      <UserSeletcor showerState={showerState} setShowerState={setShowerState} />
      <TimerWrapper>
        {startMode === StartMode.Time && (
          <TimeCountdown
            timer={timer}
            timerCountdown={timerCountdown}
            atHeader={true}
            showerState={showerState}
            setShowerState={setShowerState}
          />
        )}
        {startMode === StartMode.Volume && (
          <VolumeCountdown
            internval={CONST.VOLUME_CHECK_INTERNVAL}
            setVolume={setVolume}
            volume={volume}
            volumeCountdown={volumeCountdown}
            showerState={showerState}
            setShowerState={setShowerState}
            atHeader={true}
          />
        )}
      </TimerWrapper>
      <main className="theme-btn">
        <input onClick={() => setTheme(!isDayTheme)} className="l" type="checkbox" />
      </main>
    </Header>
  );
}

export default AppHeader;
