import React from 'react';
import { ClockCircleOutlined } from '@ant-design/icons';
import styled from 'styled-components';
import Timer from 'react-compound-timer';
import { ShowerState, StartMode } from '../../assets/const';

interface AtHeader {
  atHeader: boolean;
}

const Wrapper = styled.div<AtHeader>`
  position: relative;
  border-radius: ${(props) => (props.atHeader ? '' : '50%')};
  box-shadow: 4px 4px 16px #00b8f5, -4px -4px 16px #4268a9;
  height: ${(props) => (props.atHeader ? '100%' : '170px')};
  width: ${(props) => (props.atHeader ? '100%' : '170px')};
  font-weight: ${(props) => (props.atHeader ? '700' : '400')};
  color: ${(props) => props.theme.color.text};
  transition: all 0.5s linear;
  z-index: 1;
  cursor: pointer;
  > span {
    transition: all 0.5s linear;
    position: absolute;
    font-size: ${(props) =>
    props.atHeader ? props.theme.font.sm : props.theme.font.lg};
    z-index: 3;
    color: ${(props) => props.theme.color.text};
    transform: ${(props) =>
    props.atHeader ? 'translateX(-50%)' : 'translate(-50%, 15%)'};
    span {
      font-size: ${(props) =>
    props.atHeader ? props.theme.font.sm : props.theme.font.lg};
    }
  }
`;

interface PieProps {
  angle: number;
}

const Pie = styled.div<PieProps & AtHeader>`
  position: absolute;
  --angle: ${(props) => `${props.angle}deg`};
  border-radius: ${(props) => (props.atHeader ? '0%' : '50%')};
  background: conic-gradient(
    transparent var(--angle),
    ${(props) => props.theme.color.main} var(--angle)
  ) !important;
  height: 100%;
  width: 100%;
  z-index: 2;
`;

const PieShadow = styled.div<AtHeader>`
  color: ${(props) => props.theme.color.main};
  position: absolute;
  height: 100%;
  width: 100%;
  border-radius: ${(props) => (props.atHeader ? '0%' : '50%')};
  z-index: 1;
  background: linear-gradient(145deg, #00b8f5, #3d609d);
`;

function TimeCountDown({
  timer,
  timerCountdown,
  atHeader,
  showerState,
  setShowerState,
  setStartMode,
}: {
  timer: TimerRenderProps;
  timerCountdown: number;
  atHeader: boolean;
  setStartMode?: React.Dispatch<React.SetStateAction<StartMode>>;
} & ShowerControl<ShowerState>) {
  return (
    <Timer.Consumer>
      {() => {
        return (
          <Wrapper
            className={atHeader ? '' : 'time-start-btn-wapper'}
            atHeader={atHeader}
            onClick={() => {
              // disable start button on header
              if (atHeader || !setStartMode) return;

              // start timer
              if (showerState === ShowerState.Inited) {
                timer.start();
                setShowerState(ShowerState.Started);
                setStartMode(StartMode.Time);
              }
            }}
          >
            {/* Count down pie animation container */}
            <Pie
              className={atHeader ? '' : 'neumorphic-convex'}
              atHeader={atHeader}
              angle={
                timerCountdown === 0
                  ? 0
                  : (1 - parseInt(timer.getTime(), 10) / timerCountdown) * 360
              }
            />

            {/* Count down pie shadow */}
            <PieShadow atHeader={atHeader} />

            {/* Time display) */}
            <span>
              <Timer.Minutes />:<Timer.Seconds />
              <ClockCircleOutlined style={{ display: 'block' }} />
            </span>
          </Wrapper>
        );
      }}
    </Timer.Consumer>
  );
}

export default TimeCountDown;
